import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toggleMenu } from "../utils/appSlice";
import { YOUTUBE_SEARCH_API } from "../utils/constants";
import { cacheResults } from "../utils/searchSlice";
import { Link } from "react-router-dom";

const Head = () => {
    const dispatch = useDispatch();
    const searchCache = useSelector(store => store.search);

    const [searchQuery, setSearchQuery] = useState("");
    const [suggestions, setSuggestions] = useState([]);
    const [showSuggestions, setShowSuggestions] = useState(false);

    useEffect(() => {
        
        const timer = setTimeout(() => {
            // first looks for suggestion in cache (redux)
            if(searchCache[searchQuery]){
                setSuggestions(searchCache[searchQuery]);
            }
            else
                // debouncing at 200ms
                getSearchSuggestions();
        }, 200);

        // for clearing-up/destroying the timer vairable/function incase useEffect is called before the timeout finishes up
        // if the clean-up is ignored it WILL happen in background causing performance issues via waste API calls
        return () => {
            clearTimeout(timer);
        };
    }, [searchQuery]);



    const getSearchSuggestions = async () => {
        // API call
        // console.log(searchQuery);

        const data = await fetch(YOUTUBE_SEARCH_API + searchQuery);
        const json = await data.json();
        // console.log(json[1]);
        setSuggestions(json[1]);

        // update cache
        dispatch(cacheResults({
            [searchQuery]: json[1],
        }));
    };

    const toggleMenuHandler = () => {
        dispatch(toggleMenu());
    };

    return (
        <div className="grid grid-flow-col p-5 m-2 shadow-lg">
            <div className="flex col-span-1 cursor-pointer">
                <img className="h-8" alt="menu" src="data:image/png;base64," onClick={() => toggleMenuHandler()}/>
                <Link to="/"><img className="h-8 pl-4" alt="yt" src={require('../images/youtube-5830725_640.png')} /></Link>
            </div>
            <div className="col-span-10 mx-2 px-10">
                <input className="w-1/2 border border-gray-400 p-2 rounded-l-full" type="text" value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} onFocus={() => setShowSuggestions(true)} onBlur={() => setShowSuggestions(false)} />
                <button className="border border-gray-400 p-2 px-5 rounded-r-full bg-gray-100">Search</button>
                {showSuggestions && (<div className="fixed bg-white py-2 px-3 w-[37rem] shadow-lg rounded-lg border border-gray-100">
                    <ul>
                        {suggestions.map(s => <li className="py-2 px-3 shadow-sm hover:bg-gray-100">{s}</li>)}
                    </ul>
                </div>)}
            </div>
            <div className="col-span-1">
                <img className="h-8" alt="user-icon" src="data:image/png;base64," />
            </div>
        </div>
)};

export default Head;