import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const Sidebar = () => {
    const isMenuOpen = useSelector(store => store.app.isMenuOpen);

    // Early return pattern
    if(!isMenuOpen) return null;

    return (
        <div className="p-5 shadow-lg w-48">
            <ul>
                <li><Link to="/">Home</Link></li>
                <li><Link to="working">Shorts</Link></li>
                <li><Link to="working">Videos</Link></li>
                <li><Link to="working">Live</Link></li>
            </ul>
            <h1 className="font-bold pt-5">Subscriptions</h1>
            <ul>
                <li><Link to="working">Music</Link></li>
                <li><Link to="working">Sports</Link></li>
                <li><Link to="working">Gaming</Link></li>
                <li><Link to="working">Movies</Link></li>
            </ul>
            <h1 className="font-bold pt-5">Watch Later</h1>
            <ul>
                <li><Link to="working">Music</Link></li>
                <li><Link to="working">Sports</Link></li>
                <li><Link to="working">Gaming</Link></li>
                <li><Link to="working">Movies</Link></li>
            </ul>
        </div>
)};

export default Sidebar;